@import "../../theme.scss";
.Modal {
    position: fixed     ;
    z-index: 500;
    /* background-color: white; */
    width: 900px;
    /* height: 400px; */
    /* border: 1px solid #ccc; */
    /* box-shadow: 1px 1px 1px black; */
    /* padding-left: ; */
    /* left: 0%; */
    /* top: 0%; */
    /* box-sizing: border-box; */
    transition: all 0.3s ease-out;
    left: 49%;
}

.Modal-Left {
    position: fixed     ;
    z-index: 500;
    width: 900px;
    transition: all 0.3s ease-out;
    right: 55%;
}

.modalTestArea{
    height:'inherit';
    border-color : white !important;
    overflow-y:"auto";
    border:"none";    
}

.modalTestArea:focus,.modalTestArea:active {
    outline: none !important;
    box-shadow: none !important;
    
}

.yesButton{
    background-color:  $primary-color  !important;
    border-color:  $primary-color  !important;
    &:focus{
        outline: none !important;
        border: 1px solid $primary-color;
    }
}

.noButton{
    background-color:  $tertiary-color-2  !important;
    border-color:  $tertiary-color-2  !important;
    &:focus{
        outline: none !important;
        border: 1px solid $tertiary-color-2 ;
    }
}





@media (max-height: 800px) {
    .Modal {
        position: fixed ;
        z-index: 500;
        width: 700px;
        /* top: 100px; */
        /* height: 100px; */
        /* bottom: 100px; */
        left: 45%;
        /* font-size: .83vw; */
        transition: all 0.3s ease-out;
        /* height: 200px; */

    }

    .Modal-Left{
        position: fixed ;
        z-index: 500;
        width: 600px;
        right: 53%;
        transition: all 0.3s ease-out;
    }
    
}